.player {
  z-index: 2000;
  padding-top: 2rem;
  background: white;
  position: relative;
  .text-button {
    position: relative;
    font-size: .8rem;
    z-index: 4000;
    right: 0;
    bottom: -0.7rem;
    margin-right: 1rem;
  }
  .player-controls {
    text-align: left;
    padding-top: 2px;
  }
  .player-controls-right {
    display: inline-block;
  }
  .player-controls button:hover {
    color: $white;
    background: #B02428;
    border-bottom: 3px solid $black;
  }
  .player-controls button {
    background: none;
    border: 1px solid $black;
    border-radius: 0;
    color: black;
    border-bottom: 3px solid #B02428;
  }
  .player-progress-played[value] {
    color: #B02428;
  }
}

.hero-video-container {
  position: relative;
  bottom: 0%;
  left: 0%;
  height: 100%;
  width: 100%;
  overflow: hidden;
  background: white;
  .poster img {
    width: 100%;
    bottom: 0;
    position: absolute;
  }
  .filter {
    z-index: 100;
    position: absolute;
    background: rgba(0, 0, 0, 0.4);
    width: 100%;
  }
  .title-container {
    z-index: 1000;
    position: absolute;
    top: 35%;
    width: 100%;
    text-align: center;
    color: #fff;
  }
  .hero-text {
    color: white;
    font-size: 2rem;
    animation: changeColor 6s infinite;
  }
  @keyframes changeColor {
    0%   {color: $white;}
    50%  {color: $black;}
    100% {color: $white;}
  }
  video {
    position: absolute;
    z-index: 0;
    bottom: 0;
  }
  video.fillWidth {
    width: 100%;
  }
}

@media screen and (max-device-width: 800px) {
  .hero-video-container {
    background: url('/images/hero_short.gif') #000 no-repeat center center;
    background-size: cover;
  }
  .no-video .hero-video-container video, .touch .hero-video-container video {
    display: none;
  }
}




